@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,300,600);
@import url(https://fonts.googleapis.com/css?family=Dosis);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300);

/* @global */
*::-moz-selection {
  background-color: #fb0044;
  background-color: rgba(251, 0, 68, 0.6);
}
*::selection {
  background-color: #fb0044;
  background-color: rgba(251, 0, 68, 0.6);
}

*, :before, :after,
html *,
html:after,
html:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans';
  text-align: center;
}
/* @end global */

/* @header */
#header {
  height: 70px;
  text-align: center;
  padding: 20px 0;
}

/* @end header */

/* @hero */
.hero {
  text-align: center;
  float: none;
  min-height: calc(100vh - 50px);
}

#intro {
  position: relative;
  padding-top: 20vh;
}

#name,
#title {
  font-size: 5em;
  letter-spacing: .1em;
  color: #555;
  font-weight: 300;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  max-width: 490px;
  margin: 0 auto;
}

#title {
  font-size: 2em;
  text-align: right;
  margin-bottom: 4em;
}

#name-first,
#name-last,
#title-first,
#title-second {
  display: inline-block;
}

#name-last {
  color: #05aefc;
  color: rgba(5, 174, 252, 0.7);
}

/* TODO: maybe remove? */
#title-first {
  /*color: var(--tertiary);*/
}

#title-second {
  color: #09fc83;
  color: rgba(9, 252, 131, 0.7);
}
/* @end hero */

/* @sections */

.section-header {
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
  padding: 1em;
  font-size: 2em;
}

.section-desc {
  letter-spacing: .1em;
}

/* @end sections */
/* @about */
#about {
  position: relative;
  min-height: 100vh;
  background: #05aefc;
  background: rgba(5, 174, 252, 0.8);
  color: #eee;
}

[id^=about-text] {
  letter-spacing: .1em;
  margin: 2em auto;
  max-width: 43em;
}

#about-text-2 > a {
  margin: 0;
}
/* @end about */

/* @projects */
#projects {
  position: relative;
  min-height: 80vh;
  background: #fb0044;
  background: rgba(251, 0, 68, 0.8);
  color: #eee;
}

.project {
  padding-top: 2em;
}

.project-desc {
  padding: 1em;
}

.project a {
  font-size: 2em;
}

.project a:before,
.project a:after {
  top: 1.3em;
}

.experiments {
  min-height: 100vh;
  background-color: #09fc83;
  background-color: rgba(9, 252, 131, 0.8);
  color: #555;
}

.experiments a {
  color: #eee;
}

.experiments a:before,
.experiments a:after {
  background-color: #05AEFC;
}

#experiments {
  position: relative;
}
/* @end projects */

/* @contact */
#contact {
  min-height: 70vh;
  background-color: #05aefc;
  background-color: rgba(5, 174, 252, 0.8);
  color: #eee;
}

.contact-methods {
  padding-top: 3em;
}
/* @end contact */

/* @logos */
#logo {
  height: 60px;
  width: 60px;
  padding: 5px;
  vertical-align: bottom;
}

#logo .a,
#logo .b,
#logo .c,
#logo .d {
  stroke-width: 8px !important;
}

[class^=logo-] {
  height: 40px;
  width: 40px;
  fill: rgb(85, 85, 85);
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.logo-email {
  height: 50px;
  width: 50px;
}

[class^=logo-]:hover {
  fill: rgba(5, 174, 252, 0.7);
}

#contact [class^=logo-] {
  fill: rgb(74, 74, 74);
}

#contact [class^=logo-]:hover {
  fill: #555;
}
/* @end logos */

/* @codepen embeds */
.codepen-embeds {
  padding-top: 2em;
}

.codepen-embed {
  display: inline-block;
  width: 49%;
  overflow-x: scroll;
}
/* @end codepen embeds */
/* @links */
a {
  text-decoration: none;
  color: #555555;
  color: rgba(85, 85, 85, 0.9);
  font-size: 1.2em;
  margin: 1em;
  font-family: 'Source Sans Pro';
  position: relative;
  -webkit-transition: color .5s ease-in-out;
  transition: color .5s ease-in-out;
}

#header a:hover {
  color: rgb(82, 82, 82);
}

a:not(.arrow-link):before,
a:not(.arrow-link):after {
  content: '';
  position: absolute;
  top: 28px;
  background-color: #09FC83;
  opacity: .5;
  width: 0;
  height: 4px;
  -webkit-transition: width .3s;
  transition: width .3s;
}

a:before {
  left: 0;
}

a:after {
  right: 0;
}

a:hover:before,
a:hover:after {
  width: 50%;
}

.projects a {
  margin-left: 0;
  margin-right: 0;
}

#contact a:before,
#contact a:after {
  background-color: #eee;
}
/* @end links */

@import 'macbook.css';

.macbook {
  width: 60%;
  display: inline-block;
  max-width: 400px;
  margin: 50px auto;
  -webkit-perspective: 750;
          perspective: 750;
  -webkit-perspective-origin: 50% bottom;
          perspective-origin: 50% bottom;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.macbook-lid {
  width: 80%;
  margin: 0 auto;
  -webkit-transform-origin: 50% bottom;
          transform-origin: 50% bottom;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.macbook-lid-close {
  position: absolute;
  bottom: 5%;
  width: 100%;
  margin: 0 auto;
  padding: 2% 3% 0 3%;
  background-color: #D3D1D2;
  border-radius: 10px 10px 0 0;
}

.macbook-screen {
  position: relative;
  background-color: #353535;
  margin: 0 auto;
  padding: 3%;
  border-radius: 5px 5px 0 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.macbook-screen:before {
  display: block;
  content: '';
  position: absolute;
  top: 2%;
  left: 49%;
  width: 1%;
  padding-top: 1%;
  background-color: #000;
}

.macbook-content {
  position: relative;
  overflow: hidden;
  background-color: #323C4D;
  box-shadow: inset 0px 0px 6px #222;
}

.macbook-content:before {
  display: block;
  content: '';
  width: 1px;
  padding-top: 60%;
  float: left;
}

.macbook-content:after {
  display: block;
  content: '';
  clear: both;
}

.macbook-close .macbook-lid {
  -webkit-transform: rotate3d(-1, 0, 0, 91deg);
          transform: rotate3d(-1, 0, 0, 91deg);
}

.macbook-base {
  width: 100%;
  overflow: hidden;
  background-color: #D3D1D2;
  border-radius: 2px 2px 10px 10px;
}

.macbook-base:before {
  display: block;
  content: '';
  width: 30%;
  margin: 0 auto 1% auto;
  padding-top: 1.5%;
  background: #B0B0B0;
  border-radius: 0 0 10px 10px;
}

.macbook-base:after {
  display: block;
  content: '';
  padding-top: 1%;
  background-color: #B3B3B3;
}

.macbook-code {
  width: 50%;
  margin: 2px;
  position: absolute;
  top: 10px;
  left: 10px;
  height: 5px;
  border-radius: 20px;
  background-color: #09FC83;
}

.macbook-code:nth-of-type(2n) {
  top: 25px;
  left: 20px;
  width: 60%;
  background-color: #E8ED47;
}

.macbook-code:nth-of-type(3n) {
  top: 40px;
  left: 20px;
  width: 70%;
  background-color: #E8ED47;
}

.macbook-code:nth-of-type(4n) {
  top: 55px;
  width: 30%;
  background-color: #FB0044;
}

.macbook-code:nth-of-type(5n) {
  top: 70px;
  width: 15%;
  background-color: #05aefc;
}

.macbook-code:nth-of-type(6n) {
  top: 85px;
  left: 20px;
  width: 40%;
  background-color: #E8ED47;
}

.macbook-code:nth-of-type(7n) {
  top: 100px;
  width: 30%;
  background-color: #FB0044;
}

.macbook-code:nth-of-type(8n) {
  top: 115px;
  width: 5%;
  background-color: #05aefc;
}

.coffee {
  display: inline-block;
}

.coffee-cup {
  width: 70px;
  height: 60px;
  position: relative;
  top: -50px;
  left: 20px;
  background: -webkit-linear-gradient(right, #fff9ef 60%, #FFEAC7);
  background: linear-gradient(to left, #fff9ef 60%, #FFEAC7);
  border-radius: 0 0 30px 30px;
}

.coffee-cup:before {
  content: '';
  position: absolute;
  left: 0;
  top: -10px;
  width: 70px;
  height: 20px;
  border-radius: 50%;
  background: -webkit-radial-gradient(#4a2500, #743a00);
  background: radial-gradient(#4a2500, #743a00);
}

.coffee-cup:after {
  content: '';
  position: absolute;
  z-index: 1;
  height: 20px;
  width: 50px;
  border-radius: 10px;
  top: 10px;
  border: #fff9ef 6px solid;
}

.footer {
  background-color: #05aefc;
  background-color: rgba(5, 174, 252, 0.8);
  color: #eee;
  position: relative;
  padding: 1em;
  bottom: 0;
}

.arrow-parent {
  display: inline-block;
}

.arrow {
  height: 40px;
  width: 40px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  border-bottom: 2px solid #555;
  border-right: 2px solid #555;
  -webkit-animation: bounce 1.5s linear;
          animation: bounce 1.5s linear;
  -webkit-animation-iteration-count: 3;
          animation-iteration-count: 3;
}

@-webkit-keyframes bounce {
  15% {
    -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
    padding-bottom: 5px;
  }

  30% {
    -webkit-transform: translateY(-50%) rotate(45deg);
            transform: translateY(-50%) rotate(45deg);
  }

  40% {
    -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
    padding-bottom: 6px;
  }

  50% {
    -webkit-transform: translateY(-30%) rotate(45deg);
            transform: translateY(-30%) rotate(45deg);
  }

  70% {
    -webkit-transform: translateY(0%) rotate(45deg);
            transform: translateY(0%) rotate(45deg);
    padding-bottom: 7px;
  }

  80% {
    -webkit-transform: translateY(-15%) rotate(45deg);
            transform: translateY(-15%) rotate(45deg);
  }

  90% {
    -webkit-transform: translateY(0%) rotate(45deg);
            transform: translateY(0%) rotate(45deg);
    padding-bottom: 8px;
  }

  95% {
    -webkit-transform: translateY(-8%) rotate(45deg);
            transform: translateY(-8%) rotate(45deg);
    padding-bottom: 8px;
  }

  99% {
    -webkit-transform: translateY(-3%) rotate(45deg);
            transform: translateY(-3%) rotate(45deg);
  }

  100% {
    -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
    padding-bottom: 9px;
    opacity: 1;
  }
}

@keyframes bounce {
  15% {
    -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
    padding-bottom: 5px;
  }

  30% {
    -webkit-transform: translateY(-50%) rotate(45deg);
            transform: translateY(-50%) rotate(45deg);
  }

  40% {
    -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
    padding-bottom: 6px;
  }

  50% {
    -webkit-transform: translateY(-30%) rotate(45deg);
            transform: translateY(-30%) rotate(45deg);
  }

  70% {
    -webkit-transform: translateY(0%) rotate(45deg);
            transform: translateY(0%) rotate(45deg);
    padding-bottom: 7px;
  }

  80% {
    -webkit-transform: translateY(-15%) rotate(45deg);
            transform: translateY(-15%) rotate(45deg);
  }

  90% {
    -webkit-transform: translateY(0%) rotate(45deg);
            transform: translateY(0%) rotate(45deg);
    padding-bottom: 8px;
  }

  95% {
    -webkit-transform: translateY(-8%) rotate(45deg);
            transform: translateY(-8%) rotate(45deg);
    padding-bottom: 8px;
  }

  99% {
    -webkit-transform: translateY(-3%) rotate(45deg);
            transform: translateY(-3%) rotate(45deg);
  }

  100% {
    -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
    padding-bottom: 9px;
    opacity: 1;
  }
}

@media screen and (max-width: 520px) {
  #name {
    font-size: 3em;
  }

  .contact-links {
    margin: .5em;
  }
}

@media screen and (max-width: 787px) {
  .codepen-embed {
    width: 95%;
  }
}

@media screen and (max-height: 500px) {
  .hero {
    min-height: 120vh;
  }
}
